<template>
  <div class="basic-use">
    <h1 class="font-weight: bold;">Basic Use</h1>
    <p>
      You can place Font Awesome icons just about anywhere using a style prefix and the icon’s name. We’ve tried to make it so that icons will take on the characteristics and appear alongside text naturally.
    </p>
    <p>Font Awesome is designed to be used with inline elements and we recommend sticking with a consistent HTML element to reference them by in your project. We like the <code class="inline">&lt;i&gt;</code> tag for brevity and for the fact that most folks are using <code class="inline">&lt;em&gt;&lt;/em&gt;</code> for emphasized/italicized semantic text these days. If that’s not your cup of tea, using a <code class="inline">&lt;span&gt;</code> is more semantically correct.</p>
    <p>
      You need to know two bits of information to reference an icon, 1) its name, prefixed with fa- and 2. the style you want to use’s corresponding prefix.
    </p>
    <div v-highlight
         style="width:100%;">
      <pre class="hljs my-pre">
         <code class="html">
            &lt;div&gt;&lt;/div&gt;<!-- html代码需要转义 -->
            &lt;i class=&quot;fas fa-camera&quot;&gt;&lt;/i&gt; &lt;!-- this icon&#x27;s 1) style prefix == fas and 2) icon name == camera --&gt;
            &lt;i class=&quot;fas fa-camera&quot;&gt;&lt;/i&gt; &lt;!-- using an &lt;i&gt; element to reference the icon --&gt;
            &lt;span class=&quot;fas fa-camera&quot;&gt;&lt;/span&gt; &lt;!-- using a &lt;span&gt; element to reference the icon --&gt;
          </code>
      </pre>
    </div>
    <el-table :data="tableData"
              style="width: 100%;margin-top:0.5rem;">
      <el-table-column prop="Style"
                       label="Style"
                       width="180">
      </el-table-column>
      <el-table-column prop="Availability"
                       label="Availability"
                       width="180">
      </el-table-column>
      <el-table-column prop="StylePrefix"
                       label="Style Prefix">
      </el-table-column>
      <el-table-column prop="Example"
                       label="Example">
      </el-table-column>
      <el-table-column prop="Rendering"
                       label="Rendering">
      </el-table-column>
    </el-table>
    <el-divider></el-divider>
    <span>The fa prefix has been deprecated in version 5. The new default is the fas solid style and the fab style for brands.</span>
    <el-divider></el-divider>
    <p>
      Since Font Awesome was first implemented and continues to support being rendered using the CSS @font-face method, each of its styles corresponds to a particular font weight:
    </p>
    <el-table :data="tableData2"
              style="width: 100%;margin-top:0.5rem;">
      <el-table-column prop="Style"
                       label="Style">
      </el-table-column>
      <el-table-column prop="Availability"
                       label="Availability">
      </el-table-column>
      <el-table-column prop="fontFaceWeight"
                       label="@font-face weight">
      </el-table-column>
    </el-table>

    <span>
      <div>
        Why would I need font-weight values?
      </div>
      <p>
        While using an &lt;i &gt; or other HTML element is the most popular and easy way to render an icon, we do have advanced ways using custom written CSS pseudo-elements if you prefer to have extra markup. Font weights are used with that technique and are how you would set the icon's style.
      </p>
    </span>
    <el-divider></el-divider>
    <div>
      <h2>Ligature use in Font Awesome on the web</h2>
      <p>
        While our desktop workflow takes advantage of ligatures to make inserting icons into your favorite design and presentation apps easy as well as our SVG + JS framework, ligature lookup tables take up additional file space and can cause performance issues. Until we have options and solutions for subsetting icons, we aren't comfortable considering ligatures for Web Fonts + CSS.
      </p>
    </div>
    <el-divider></el-divider>
    <div>
      <h1>Font Awesome Icons + Your Project’s Styling</h1>
      <p>Font Awesome icons automatically inherit CSS size and color. This means they blend in with text inline wherever you put them. Font Awesome tries not to be too opinionated, and sets just the basic styling rules icons need to render properly in context.</p>
    </div>

    <div v-highlight
         style="width:100%;">
      <pre class="hljs my-pre">
         <code class="html">
            &lt;span style=&quot;font-size: 3em; color: Tomato;&quot;&gt;
            &lt;i class=&quot;fas fa-camera&quot;&gt;&lt;/i&gt;
            &lt;/span&gt;

            &lt;span style=&quot;font-size: 48px; color: Dodgerblue;&quot;&gt;
            &lt;i class=&quot;fas fa-camera&quot;&gt;&lt;/i&gt;
            &lt;/span&gt;

            &lt;span style=&quot;font-size: 3rem;&quot;&gt;
            &lt;span style=&quot;color: Mediumslateblue;&quot;&gt;
            &lt;i class=&quot;fas fa-camera&quot;&gt;&lt;/i&gt;
            &lt;/span&gt;
            &lt;/span&gt;
            </code>
      </pre>
    </div>
    <el-divider></el-divider>
    <div>
      <h1>
        Additional Styling Options
      </h1>
      <p>
        While the basic way to reference an icon is simple and hopefully straight-forward, we’ve provided support-level styling for things like sizing icons, aligning and listing icons, as well as rotating and transforming icons.
      </p>
      <p>
        You can also add your own custom styling to Font Awesome icons by adding your own CSS rules in your project’s code. Here’s a quick example.
      </p>
    </div>

    <div v-highlight
         style="width:100%;">
      <pre class="hljs my-pre">
         <code class="html">
            &lt;head&gt;
              &lt;!-- reference your copy Font Awesome here (from our CDN or by hosting yourself) --&gt;
              &lt;link href=&quot;/your-path-to-fontawesome/css/fontawesome.css&quot; rel=&quot;stylesheet&quot;&gt;
              &lt;link href=&quot;/your-path-to-fontawesome/css/brands.css&quot; rel=&quot;stylesheet&quot;&gt;
              &lt;link href=&quot;/your-path-to-fontawesome/css/solid.css&quot; rel=&quot;stylesheet&quot;&gt;

              &lt;!-- custom styling for all icons --&gt;
              i.fas,
              i.fab {
                border: 1px solid red;
              }

              &lt;!-- custom styling for specific icons --&gt;
              .fa-fish {
                color: salmon;
              }

              .fa-frog {
                color: green;
              }

              .fa-user-ninja.vanished {
                opacity: 0.0;
              }

              .fa-facebook {
                color: rgb(59, 91, 152);
              }
            &lt;/head&gt;
            &lt;body&gt;
              &lt;i class=&quot;fas fa-fish&quot;&gt;&lt;/i&gt;
              &lt;i class=&quot;fas fa-frog&quot;&gt;&lt;/i&gt;
              &lt;i class=&quot;fas fa-user-ninja vanished&quot;&gt;&lt;/i&gt;
              &lt;i class=&quot;fab fa-facebook&quot;&gt;&lt;/i&gt;
            &lt;/body&gt;
            </code>
      </pre>
    </div>
    <el-divider></el-divider>
    <div>
      <h1>
        Writing Custom CSS with our SVG + JS framework
      </h1>
      <p>
        When using our SVG framework, remember that Font Awesome-based &lt;i&gt; DOM elements are replaced with new &lt;svg &gt; elements by default. Be sure that your css rules target the right elements.
      </p>
    </div>
