            <code class="javascript">
                [javascript]
                var a = 123;
                var b = document.getElementById('tr')
            </code>

        </pre>
      <div>
        <pre class="hljs my-pre">
            <code class="html">
          [HTML]
          &lt;span style=&quot;font-size: 3em; color: Tomato;&quot;&gt;
          &lt;i class=&quot;fas fa-camera&quot;&gt;&lt;/i&gt;
          &lt;/span&gt;

          &lt;span style=&quot;font-size: 48px; color: Dodgerblue;&quot;&gt;
          &lt;i class=&quot;fas fa-camera&quot;&gt;&lt;/i&gt;
          &lt;/span&gt;

          &lt;span style=&quot;font-size: 3rem;&quot;&gt;
          &lt;span style=&quot;color: Mediumslateblue;&quot;&gt;
          &lt;i class=&quot;fas fa-camera&quot;&gt;&lt;/i&gt;
          &lt;/span&gt;
          &lt;/span&gt;
            </code>
        </pre>
      </div>
      <div style="margin-top:1rem;">
        <pre class="hljs my-pre">
            <code class="css">
                [CSS]
                {
                    width:'100px'
                }
            </code>
        </pre>
      </div> -->
    <el-row>
      <el-col :xs="24"
              :sm="24"
              :md="24"
              :lg="24"
              :xl="24">
      </el-col>
    </el-row>
  </div>

</template>
<script>
export default {
  data () {
    return {
      tableData: [{
        Style: 'Solid',
        Availability: 'Free',
        StylePrefix: 'fas',
        Example: '<i class="fas fa-camera"></i>',
        Rendering: 'Rendering'
      }, {
        Style: 'Light',
        Availability: 'Pro Required',
        StylePrefix: 'fal',
        Example: '<i class="fal fa-camera"></i>',
        Rendering: 'Rendering'
      }, {
        Style: 'Duotone',
        Availability: 'Pro Required',
        StylePrefix: 'fad',
        Example: '<i class="fad fa-camera"></i>',
        Rendering: 'Rendering'
      }, {
        Style: 'Brands',
