<template>
  <div class="header">
    <header class="header-list">
      <nav v-for="(item,index) in headerList"
           :key="index">
        <a style="cursor: pointer;font-weight: 600;"
           :class="[item==clickActive?'home-active':'']"
           @click="goTo(item)"> {{item}}</a>
      </nav>
    </header>
  </div>
</template>
<script>
export default {
  data () {
    return {
      clickActive: 'Home',
      headerList: [
        'Home', 'Icons', 'Docs', 'Support'
      ]
    }
  },
  created () {
    this.headerList.map((item) => {
      var router = window.location.href
      console.log(router)
      if (router.indexOf(item.toLowerCase()) !== -1) {
        console.log(item)
        this.clickActive = item
      }
    })
  },
  methods: {
    goTo (item) {
      this.clickActive = item
      localStorage.setItem('home-active', item)
      var routerNames = item.toLocaleLowerCase()
      if (this.$route.path.search(routerNames) !== -1) {
        console.log('包含')
        return
      }
      this.$router.push({ // 核心语句
        path: '/' + (routerNames === 'docs' ? 'docs/BasicUse' : routerNames), // 跳转的路径
        query: { // 路由传参时push和query搭配使用 ，作用时传递参数
          id: this.id
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
@import "~@/theme/index.less";
.header {
  height: 1.5rem;
  background-color: #155592;
  display: flex;
  align-items: center;
  width: 100%;
  .header-list {
    width: 100%;
    display: flex;
    justify-content: space-around;
    color: @index-font-color;
    font-size: 16px;
    font-weight: 600;
    > nav {
      padding: 0.2rem;
      border-radius: 0.1rem;
      &:hover {
        background-color: #4d56d8;
      }
    }
  }
}
@media only screen and (max-width: 767px) {
  .header {
    background-color: #74b816;
    .header-list {
    }
  }
}
.home-active {
  color: yellow;
}
</style>
