import Vue from 'vue'
import Router from 'vue-router'
import GettingStarted from '../views/docs/SetUp/GettingStarted'
import HostingFontAwesomeYourself from '../views/docs/SetUp/HostingFontAwesomeYourself'
import UpgradingfromVersion4 from '../views/docs/SetUp/UpgradingfromVersion4'
import UsingaPackageManager from '../views/docs/SetUp/UsingaPackageManager'

import BasicUse from '../views/docs/ReferencingIcons/BasicUse'
import TheIconCheatsheet from '../views/docs/ReferencingIcons/TheIconCheatsheet'

import SizingIcons from '../views/docs/Styling/SizingIcons'
import FixedWidthIcons from '../views/docs/Styling/Fixed-WidthIcons'
import IconsinaList from '../views/docs/Styling/IconsinaList'
import RotatingIcons from '../views/docs/Styling/RotatingIcons'
import AnimatingIcons from '../views/docs/Styling/AnimatingIcons'
import BorderedPulledIcons from '../views/docs/Styling/Bordered+PulledIcons'
import StackingIcons from '../views/docs/Styling/StackingIcons'
import PowerTransforms from '../views/docs/Styling/PowerTransforms'
import MaskingIcons from '../views/docs/Styling/MaskingIcons'
import LayeringTextandCounters from '../views/docs/Styling/Layering,Text,andCounters'
import DuotoneIcons from '../views/docs/Styling/DuotoneIcons'

import Angular from '../views/docs/UsingFontAwesomeWith/Angular'
import Ember from '../views/docs/UsingFontAwesomeWith/Ember'
import jQuery from '../views/docs/UsingFontAwesomeWith/jQuery'
import Less from '../views/docs/UsingFontAwesomeWith/Less'
import React from '../views/docs/UsingFontAwesomeWith/React'
import ReactNative from '../views/docs/UsingFontAwesomeWith/ReactNative'
import Sass from '../views/docs/UsingFontAwesomeWith/Sass'
import Turbolinks from '../views/docs/UsingFontAwesomeWith/Turbolinks'
import Vuejs from '../views/docs/UsingFontAwesomeWith/Vue.js'
import WordPress from '../views/docs/UsingFontAwesomeWith/WordPress'

import CSSPseudoElements from '../views/docs/Advanced/CSSPseudo-Elements'
import SVGSprites from '../views/docs/Advanced/SVGSprites'
import SVGSymbolsJavaScript from '../views/docs/Advanced/SVGSymbols(JavaScript)'
import SVGJavaScriptCore from '../views/docs/Advanced/SVGJavaScriptCore'
import SVGAsynchronousLoading from '../views/docs/Advanced/SVGAsynchronousLoading'

import ConflictDetection from '../views/docs/OtherTopics/ConflictDetection'
import Accessibility from '../views/docs/OtherTopics/Accessibility'
import BrowserSupport from '../views/docs/OtherTopics/BrowserSupport'
import Performance from '../views/docs/OtherTopics/Performance'
import Security from '../views/docs/OtherTopics/Security'
import ServerSideRendering from '../views/docs/OtherTopics/ServerSideRendering'
import Troubleshooting from '../views/docs/OtherTopics/Troubleshooting'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      // redirect: {
      //   name: 'home' // 重定向
      // }
      name: 'index',
      alias: '/home', // 别名
      component: () =>
        import(/* webpackChunkName: "about" */ '../views/home/home.vue')
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import('../views/home/about/about.vue')
    },
    {
      path: '/docs',
      name: 'docs',
      component: () => import('../views/docs/docs.vue'),
      children: [
        {
          path: 'GettingStarted',
          component: GettingStarted
        },
        {
          path: 'HostingFontAwesomeYourself',
          component: HostingFontAwesomeYourself
        },
        {
          path: 'UpgradingfromVersion4',
          component: UpgradingfromVersion4
        },
        {
          path: 'UsingaPackageManager',
          component: UsingaPackageManager
        },
        {
          // 当 /user/:id/profile 匹配成功，
          // UserProfile 会被渲染在 User 的 <router-view> 中
          path: 'BasicUse',
          component: BasicUse
        },
        {
          path: 'TheIconCheatsheet',
          component: TheIconCheatsheet
        },
        {
          path: 'SizingIcons',
          component: SizingIcons
        },
        {
          path: 'Fixed-WidthIcons',
          component: FixedWidthIcons
        },
        {
          path: 'IconsinaList',
          component: IconsinaList
        },
        {
          path: 'RotatingIcons',
          component: RotatingIcons
        },
        {
          path: 'AnimatingIcons',
          component: AnimatingIcons
        },
        {
          path: 'Bordered+PulledIcons',
          component: BorderedPulledIcons
        },
        {
          path: 'StackingIcons',
          component: StackingIcons
        },
        {
          path: 'PowerTransforms',
          component: PowerTransforms
        },
        {
          path: 'MaskingIcons',
          component: MaskingIcons
        },
        {
          path: 'Layering,Text,andCounters',
          component: LayeringTextandCounters
        },
        {
          path: 'DuotoneIcons',
          component: DuotoneIcons
        },

        {
          path: 'Angular',
          component: Angular
        },
        {
          path: 'Ember',
          component: Ember
        },
        {
          path: 'jQuery',
          component: jQuery
        },
        {
          path: 'Less',
          component: Less
        },
        {
          path: 'React',
          component: React
        },
        {
          path: 'ReactNative',
          component: ReactNative
        },
        {
          path: 'Sass',
          component: Sass
        },
        {
          path: 'Turbolinks',
          component: Turbolinks
        },
        {
          path: 'Vue.js',
          component: Vuejs
        },
        {
          path: 'WordPress',
          component: WordPress
        },

        {
          path: 'CSSPseudo-Elements',
          component: CSSPseudoElements
        },
        {
          path: 'SVGSprites',
          component: SVGSprites
        },
        {
          path: 'SVGSymbols(JavaScript)',
          component: SVGSymbolsJavaScript
        },
        {
          path: 'SVGJavaScriptCore',
          component: SVGJavaScriptCore
        },
        {
          path: 'SVGAsynchronousLoading',
          component: SVGAsynchronousLoading
        },
        {
          path: 'ConflictDetection',
          component: ConflictDetection
        },
        {
          path: 'Accessibility',
          component: Accessibility
        },
        {
          path: 'BrowserSupport',
          component: BrowserSupport
        },
        {
          path: 'Performance',
          component: Performance
        },
        {
          path: 'Security',
          component: Security
        },
        {
          path: 'ServerSideRendering',
          component: ServerSideRendering
        },
        {
          path: 'Troubleshooting',
          component: Troubleshooting
        }
      ]
    },
    {
      path: '/icons',
      name: 'icons',
      component: () => import('../views/icons/icons.vue')
    },
    {
      path: '/support',
      name: 'support',
      component: () => import('../views/support/support.vue')
    }
  ],
  linkActiveClass: 'is-active'
})
