const util = {
  pagingClick (vueThis, names, fangxiang) {
    var toRouter = names.replace(/\s/g, '')
    var childNum = ''
    var fatherNum = ''
    if (fangxiang === 'right') {
      console.log(vueThis.$store.state.fatherNum)
      console.log(vueThis.$store.state.childNum)
      fatherNum = vueThis.$store.state.fatherNum
      childNum = vueThis.$store.state.childNum + 1
      console.log(
        vueThis.$store.state.getDaoHaoList[vueThis.$store.state.fatherNum].list
          .length
      )
      if (
        vueThis.$store.state.childNum + 1 >=
        vueThis.$store.state.getDaoHaoList[vueThis.$store.state.fatherNum].list
          .length
      ) {
        fatherNum = vueThis.$store.state.fatherNum + 1
        childNum = 0
      }
    } else {
      console.log('后退')
      fatherNum = vueThis.$store.state.fatherNum
      childNum = vueThis.$store.state.childNum - 1
      if (childNum < 0) {
        fatherNum = vueThis.$store.state.fatherNum - 1
        childNum = vueThis.$store.state.getDaoHaoList[fatherNum].list.length - 1
      }
      console.log(fatherNum, childNum)
    }
    var payload = {
      fatherNum,
      childNum
    }
    vueThis.$store.commit('increment', payload)
    vueThis.$store.commit('getclickActive', toRouter)
    vueThis.$router.push('/docs/' + toRouter)
  }
}
export default util
