<template>
  <div>
    <h1>{{i18n.Turbolinks.Turbolinks}}</h1>
    <p>{{i18n.Turbolinks.FontAwesome}}</p>
    <h1>{{i18n.Turbolinks.This}}</h1>
    <p>{{i18n.Turbolinks.Ifyou}}</p>
    <h1>{{i18n.Turbolinks.Somechallenges}}</h1>
    <p>{{i18n.Turbolinks.After}}</p>
    <ul v-for="(item,index) in i18n.Turbolinks.dataList"
        :key="index">
      <li>{{index+1}}.{{item}}</li>
    </ul>
    <p>{{i18n.Turbolinks.WhenTurbolinks}}</p>
    <p>{{i18n.Turbolinks.Here}}</p>
    <p>{{i18n.Turbolinks.Setting}}</p>
    <h1>{{i18n.Turbolinks.WithRails}}</h1>
    <p>{{i18n.Turbolinks.going}}</p>
    <h1>{{i18n.Turbolinks.CDN}}</h1>
    <p>{{i18n.Turbolinks.installed}}</p>
    <p>{{i18n.Turbolinks.Add}}</p>
    <div v-highlight
         style="width:100%;">
      <pre class="hljs my-pre">
         <code class="html">
            &lt;!DOCTYPE html&gt;
            &lt;html&gt;
              &lt;head&gt;
                &lt;title&gt;My app&lt;/title&gt;
                &lt;%= csrf_meta_tags %&gt;
                &lt;%= csp_meta_tag %&gt;

                &lt;%= stylesheet_link_tag    &#x27;application&#x27;, media: &#x27;all&#x27;, &#x27;data-turbolinks-track&#x27;: &#x27;reload&#x27; %&gt;
                &lt;%= javascript_include_tag &#x27;application&#x27;, &#x27;data-turbolinks-track&#x27;: &#x27;reload&#x27; %&gt;
                &lt;script src=&quot;https://use.fontawesome.com/releases/v5.11.2/js/all.js&quot; data-mutate-approach=&quot;sync&quot;&gt;&lt;/script&gt;
              &lt;/head&gt;

              &lt;body&gt;
                &lt;%= yield %&gt;
              &lt;/body&gt;
            &lt;/html&gt;
          </code>
      </pre>
    </div>
    <h1>{{i18n.Turbolinks.pipeline}}</h1>
    <p>{{i18n.Turbolinks.Another}}</p>
    <p>{{i18n.Turbolinks.configuration}}</p>
    <div v-highlight
         style="width:100%;">
      <pre class="hljs my-pre">
         <code class="html">
              &lt;!DOCTYPE html&gt;
              &lt;html&gt;
                &lt;head&gt;
                  &lt;title&gt;My app&lt;/title&gt;
                  &lt;%= csrf_meta_tags %&gt;
                  &lt;%= csp_meta_tag %&gt;

                  &lt;script&gt;
                    FontAwesomeConfig = {
                      mutateApproach: &#x27;sync&#x27;
                    }
                  &lt;/script&gt;

                  &lt;%= stylesheet_link_tag    &#x27;application&#x27;, media: &#x27;all&#x27;, &#x27;data-turbolinks-track&#x27;: &#x27;reload&#x27; %&gt;
                  &lt;%= javascript_include_tag &#x27;application&#x27;, &#x27;data-turbolinks-track&#x27;: &#x27;reload&#x27; %&gt;
                &lt;/head&gt;

                &lt;body&gt;
                  &lt;%= yield %&gt;
                &lt;/body&gt;
              &lt;/html&gt;
          </code>
      </pre>
    </div>
    <p>{{i18n.Turbolinks.But}}</p>
    <div v-highlight
         style="width:100%;">
      <pre class="hljs my-pre">
         <code class="javascript">
            // This is a manifest file that&#x27;ll be compiled into application.js, which will include all the files
            // listed below.
            //
            // Any JavaScript/Coffee file within this directory, lib/assets/javascripts, or any plugin&#x27;s
