      Make:
        'Make sure you’re using the correct custom property values we’ve defined. Also, make sure you haven’t defined a custom property that may be overriding things (check inline and in your custom CSS). We don’t recommend writing custom CSS to target a specific class or pseudo element - there are some differences between our Web Fonts and SVG versions of Font Awesome that can make this tough.',
