    WordPress: {
      WordPress: 'WordPress',
      FontAwesome:
        'Font Awesome has an official WordPress plugin that’s available for those who want to easily use our icons in their projects. We can also show you how to customize WordPress without a plugin.',
      Choose: 'Choose Your Own Adventure',
      p1:
        'Setting up Font Awesome in WordPress can be relatively simple. However, in our experience working with customers using WordPress, the more challenging problem is dealing with the conflicts introduced by themes and plugins that try to load their own versions of Font Awesome. Our icons are so popular in the WordPress ecosystem, we see this problem with conflicts show up quite often.',
      p2:
        'Our goal is not only to make it simple for you to set up Font Awesome in WordPress, but also to make your setup robust enough to work with your theme and plugins that try to load their own Font Awesome, so that they all just work—using your preferred version of Font Awesome.',
      p3:
        'So we offer two different ways for you to go about set up: our plugin, or customizing WordPress without a plugin.',
      p4:
        'Both are are designed for the real world of WordPress where conflicts abound. The plugin approach handles a lot for you without you having to touch any PHP code; the customizing approach requires more manual effort on your part, while minimizing the amount of PHP code you need to modify.',
      Which: 'Which Should I Choose?',
      p5:
        "If you're not sure, go with the plugin. It's easy to install and handles a lot of things for you, and as we update it with improved functionality and features, it'll only get better and better with no extra effort on your part.",
      p6: 'Go custom if:',
      dataList: [
        'You know that your site has zero or minimal Font Awesome conflicts from your theme or plugins.Want to find out? Try Conflict Detection.',
        "Installing another plugin is something you'd like to avoid.",
        "Installing additional plugins is something you normally avoid, so that you're unlikely to introduce conflicts with later plugin additions.",
        "You're comfortable with modifying your theme's functions.php file to customize WordPress.",
        'You want to use kits now.'
      ],
      p7: "(We're working on adding kits support to the plugin.)",
      Plugin: 'Plugin Setup',
      Release: 'Release Candidate',
      Ourplugin:
        'Our plugin is still in pre-release mode and needs your feedback! If you have problems or questions email us a hello@fontawesome.com',
      Where: 'Where to find the plugin',
      Theplugin: 'The plugin is available from WordPress Plugins page.',
      HowtoInstall: 'How to Install',
      Available: 'Available from wordpress.org',
      dataList2: [
        'Login to your WordPress Admin',
        'Click “Plugins” in the left menu',
        'Click “Add new”',
        'Search by “Author” for “fontawesome”',
        'Click “Install Now” on the “Font Awesome” plugin By Font Awesome',
        'Click “Activate”'
      ],
      ClickAddnew: 'Click `Add new`',
      search: 'Search for `Font Awesome`',
      result: 'Click `Install Now`',
      activate: "Click `Activate` and you're finished!",
      HowtoUse: 'How to Use',
      Visit: 'Visit the plugin page to learn more.',
      CDN: 'Using the plugin with the Pro CDN',
      ProCDN: 'Pro Subscribers have access to the Pro CDN.',
      Addyour: 'Add your domains to your Font Awesome Account Domains',
      Note: 'Note you need a Pro subscription to do this next step',
      dataList3: [
        'Go to https://fontawesome.com/account/cdn',
        'Add domains that you’d like to use with your Pro subscription'
      ],
      Enable: 'Enable Pro CDN in your WordPress installation:',
      dataList4: [
        'Go to your WordPress Admin “Settings” and select “Font Awesome”',
        'Toggle the “Use Pro” option',
        'Click “Save Changes”'
      ],
      That: 'That’s it! You’ve got it setup.',
      WordPressPages: 'Add Icons to WordPress Pages, Posts, Templates',
      dataList5: [
        'Add icons using tags like <i class="fas fa-stroopwafel"></i> in your WordPress content.',
        'There’s also a shortcode that can be used like this: [icon name="stroopwafel"]',
        'Refer to the plugin’s readme in the plugins directory for more details.'
      ],
      Heads: 'Heads up about Common Compatibility Issues',
      dataList6: [
        'Font Awesome is quite popular among WordPress themes and plugins. Your theme or another plugin you’re using might be trying to install its own version of Font Awesome different than the one you’ve just installed. This can cause confusing and unpredictable results. But if you want to use the latest icons, or especially if you want to be able to use Pro icons that you’ve purchased, then it’s important to resolve the conflicts in such a way that your Font Awesome installation is the one being used.',
        'Our plugin can automatically resolve many of those problems if you enable the option to “Remove unregistered clients” on the plugin’s admin settings page.',
        'Some themes and plugins have their own icon choosers that are populated only with the icons they know about. In that case, your Pro icons probably won’t show up in those icon choosers. We’re hoping to create better coordination between themes and plugins to make that a smoother experience for you.',
        'We’d love to be in conversation with other theme and plugin developers about how to improve compatibility. Developers can shoot us a message at hello@fontawesome.com to get that conversation going.'
      ]
    }
