import Vue from 'vue'
import App from './App.vue'
import router from './router/router'
import store from './store'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import VueI18n from 'vue-i18n'
import VueLazyload from 'vue-lazyload'
import util from './util/util'
import paging from './components/paging.vue' // 引入公共组件 paging 翻页
import { library } from '@fortawesome/fontawesome-svg-core'

import { fas } from '@fortawesome/free-solid-svg-icons'

import { far } from '@fortawesome/free-regular-svg-icons'
import { fab } from '@fortawesome/free-brands-svg-icons'
import {
  FontAwesomeIcon,
  FontAwesomeLayers,
  FontAwesomeLayersText
} from '@fortawesome/vue-fontawesome'
import VueClipboard from 'vue-clipboard2'
import hljs from 'highlightjs'
import 'highlight.js/styles/vs2015.css' // 引入公共类
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: require('./assets/error.png'),
  loading: require('./assets/loading.png'),
  attempt: 1
}) // 图片懒加载
Vue.use(VueLazyload) // 样式文件
Vue.component('paging', paging) // 全局注册docs翻页

Vue.directive('highlight', function (el) {
  let blocks = el.querySelectorAll('pre code')
  blocks.forEach(block => {
    hljs.highlightBlock(block)
  })
})
Vue.use(VueClipboard)
Vue.use(ElementUI)
Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: 'en', // 语言标识
  messages: {
    cn: require('./language/zh.js'),
    en: require('./language/en.js')
  }
})

library.add(fas, far, fab)
Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.component('font-awesome-layers', FontAwesomeLayers)
Vue.component('font-awesome-layers-text', FontAwesomeLayersText)
Vue.config.productionTip = false
Vue.prototype.$util = util // 公共类
new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
