
<template>
  <el-row class="paging"
          type="flex">
    <el-col :xs="12"
            :sm="12"
            :md="12"
            :lg="12"
            :xl="12"
            class="left-page"
            v-show="leftNames">
      <font-awesome-icon :icon="['fas', 'arrow-left']" />
      <a @click="pagingClick(leftNames.replace(/\s/g, ''),'left')">{{leftNames}}</a>
    </el-col>
    <el-col :xs="12"
            :sm="12"
            :md="12"
            :lg="12"
            :xl="12"
            class="right-page"
            v-show="rightNames">
      <a @click="pagingClick(rightNames.replace(/\s/g, ''),'right')">{{rightNames}}</a>
      <font-awesome-icon :icon="['fas', 'arrow-right']" />
    </el-col>
  </el-row>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  data () {
    return {
    }
  },
  created () {
    // console.log(this.fatherNum)
    // this.add()
  },
  props: {
    // leftNames: String,
    // rightNames: String
  },
  components: {},
  computed: {
    leftNames () {
      if (this.$store.state.childNum === 0) {
        // 上一个数组的最后一项
        console.log(this.$store.state.fatherNum)
        if (this.$store.state.fatherNum === 0) {
          return ''
        }
        var list = this.$store.state.getDaoHaoList[this.$store.state.fatherNum - 1].list
        return list[list.length - 1].child
      }
      return this.$store.state.getDaoHaoList[this.$store.state.fatherNum].list[this.$store.state.childNum - 1].child
    },
    rightNames () {
      if (this.$store.state.childNum + 1 >= this.$store.state.getDaoHaoList[this.$store.state.fatherNum].list.length) {
        if (this.$store.state.fatherNum === 5) {
          console.log(this.$store.state.childNum + 1)
          return ''
        }
        return this.$store.state.getDaoHaoList[this.$store.state.fatherNum + 1].list[0].child
      }
      return this.$store.state.getDaoHaoList[this.$store.state.fatherNum].list[this.$store.state.childNum + 1].child
    },
    getDaoHaoList () {
      console.log(this.$store.state.getDaoHaoList[this.$store.state.fatherNum].list)
      return this.$store.state.getDaoHaoList[this.$store.state.fatherNum].list[this.$store.state.childNum].child
    },
    i18n () {
      return this.$t('docs')
    }
  },
  methods: {
    pagingClick (names, fangxaing) {
      this.$util.pagingClick(this, names, fangxaing)
    },
    // ...mapActions({
    //   add: 'increment' // 将 `this.add()` 映射为 `this.$store.dispatch('increment')`
    // }),
    ...mapActions({
      add: 'increment' // 将 `this.add()` 映射为 `this.$store.commit('increment')`
    })
  }
}

</script>
<style lang='less' scoped>
.paging {
  // background-color: red;
  color: #4dabf7;
  font-size: 16px;
  text-align: center;
  margin-top: 1rem;
  flex-wrap: wrap;

  a {
    color: #4dabf7;
    font-size: 18px;
    margin-left: 10px;
    margin-right: 10px;
    cursor: pointer;
  }

  .left-page {
  }

  .right-page {
  }
}
</style>
