import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    count: 1,
    fatherNum: 1,
    childNum: 0,
    getDaoHaoList: [],
    clickActive: 'BasicUse'
  },
  getters: {
    doneTodos: state => {
      return state.todos.filter(todo => todo.done)
    }
  },
  mutations: {
    increment (state, payload) {
      // mutations同步
      // state.count++
      console.log('fatherNum:', payload.fatherNum)
      console.log('childNum:', payload.childNum)
      state.fatherNum = payload.fatherNum
      state.childNum = payload.childNum
    },
    getDaoHaoList (state, payload) {
      console.log(payload)
      state.getDaoHaoList = payload
    },
    getclickActive (state, payload) {
      state.clickActive = payload
    }
  },
  actions: {
    // actions异步
    increment ({ commit }, payload) {
      console.log(payload)
      console.log('fatherNum:', payload.fatherNum)
      console.log('childNum:', payload.childNum)
      // setTimeout(() => {
      commit('increment', payload)
      // }, 1000)
    },
    getDaoHaoList ({ commit }, payload) {
      commit('getDaoHaoList', payload)
    },
    getclickActive ({ commit }, payload) {
      commit('getclickActive', payload)
    }
  }
})
// store.commit('increment')

